import * as React from 'react';
import { Route, Routes } from 'react-router-dom';

import { Response } from '@miq/utiljs';

import StaffView from '../Views';
import { StaffSection } from '../components';

import { useDocumentTitle } from '@miq/hookjs';
import { useHitListRequest } from './hits';

export default function IndexView() {
  useDocumentTitle('Analytics');
  const [d, setD] = React.useState();

  React.useEffect(() => {
    const data = navigator;
    setD(data.language);

    console.log(JSON.stringify(data?.language));
  }, []);

  const { res } = useHitListRequest();

  const r = new Response(res);

  if (!r.isSuccess) return null;

  return (
    <StaffView mainCN="no-bg p-0">
      <div className="mt-3">
        summary
        {/* <HitSummarySection /> */}
      </div>

      <StaffSection title="Last 100 hits">
        list
        {/* <HitList items={r.items} /> */}
      </StaffSection>

      <pre>{d && JSON.stringify(d)}</pre>
    </StaffView>
  );
}
